<template>
  <el-dialog title="增加题目" :visible.sync="dialogFormAddSubjectVisible"  :show-close="false">
            <el-form :model="form">
                <el-form-item label="所属章节">
                  <el-select v-model="key1" placeholder="请选择..." @change="getPoint(key1)">
                    <el-option
                      v-for="item in chapterData"
                      :key="item.id"
                      :key1="item.id"
                      :label="item.value"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
              <el-form-item label="难易等级(输入对应数字！)">
                <el-input type="number" v-model="form.difficult" placeholder="1.简单、2.一般、3.困难、4.特别困难"></el-input>
              </el-form-item>
                <el-form-item label="知识点">
                  <el-select v-model="key2" placeholder="请选择...">
                    <el-option
                      v-for="item in pointData"
                      :key="item.id"
                      :key2="item.id"
                      :label="item.value"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="问题描述">
                    <el-input type="textarea" v-model="form.question"></el-input>
                </el-form-item>
              <div v-if="showChoice">
                <el-form-item label="A">
                    <el-input v-model="form.A"></el-input>
                </el-form-item>
                <el-form-item label="B">
                    <el-input v-model="form.B"></el-input>
                </el-form-item>
                <el-form-item label="C">
                    <el-input v-model="form.C"></el-input>
                </el-form-item>
                <el-form-item label="D">
                    <el-input v-model="form.D"></el-input>
                </el-form-item>
                <el-form-item label="E" v-if="showMuilt">
                    <el-input v-model="form.E"></el-input>
                </el-form-item>
              </div>
                <el-form-item label="答案">
                    <el-input type="textarea" v-model="form.answer"></el-input>
                </el-form-item>
              <el-form-item label="分值(不含小数的纯数字)">
                <el-input type="number" v-model="form.score"></el-input>
              </el-form-item>
                <el-form-item label="备注">
                    <el-input type="textarea" v-model="form.faq"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="handleClose">取 消</el-button>
                <el-button type="primary" @click="handleAddClose">保 存</el-button>
            </div>
        </el-dialog>
</template>

<script>
    import bus from './BUS.vue'
    import { Loading } from 'element-ui';

    export default {
        data () {
           return {
             key1:'',
             key2:'',
             showMuilt:false,
             showChoice:false,
             chapterData:[
               {
                 "id":1,
                 "value":"第一章 计算机系统概述"
               },
               {
                 "id":2,
                 "value":"第二章 计算机的发展及应用"
               },
               {
                 "id":3,
                 "value":"第三章 系统总线"
               },
               {
                 "id":4,
                 "value":"第四章 存储器"
               },
               {
                 "id":5,
                 "value":"第五章 输入输出系统"
               },
               {
                 "id":6,
                 "value":"第六章 计算机的运算方法"
               },
               {
                 "id":7,
                 "value":"第七章 指令系统"
               },
               {
                 "id":8,
                 "value":"第八章 CPU的结构和功能"
               },{
                 "id":9,
                 "value":"第九章 控制单元的功能"
               },
               {
                 "id":10,
                 "value":"第十章 控制单元的设计"
               }
             ],
             pointData:[],
                form:{
               score:'',
                    difficult:'',
                    courseId:'',
                    pointId:'',
                    question:'',
                    A:'',
                    B:'',
                    C:'',
                    D:'',
                    E:'',
                    imgName:'',
                    answer:'',
                    faq:'',
                    questionImg:{},
                    answerImg:{}
                },
                dialogFormAddSubjectVisible: false,
                question_fileList:[],
                answer_fileList:[],
                type:'',
                isLast:false,
                questionImgCounter:0,
                answerImgCounter:0,
                questionImgNameAry:[],
                answerImgNameAry:[],
           }
        },
        watch:{
            "this.form": function (newValue) {
                console.log(newValue);
            }
        },
        methods:{
          getPoint(val){
            this.key2='';
            this.axios.get('http://localhost:8089/point/getPoint',{
              params:{
                chapterId:val
              }
            })
            .then(res =>{
              this.pointData = [];
              this.pointData = res.data.data;
            })
          },
            handleAddClose(){  //点击保存按钮后把添加的内容发送给服务器
                this.dialogFormAddSubjectVisible = false;
                var loadingInstance = Loading.service({   //读取文件时显示加载条
                    target:'#container',
                    text:'题目上传中...'
                });
                var params = new URLSearchParams();
                params.append('type',this.type);
                params.append('difficult',this.form.difficult);
                params.append('courseId',this.key1);
                params.append('pointId',this.key2);
                params.append('question',this.form.question);
                params.append('A',this.form.A);
                params.append('B',this.form.B);
                params.append('C',this.form.C);
                params.append('D',this.form.D);
                params.append('E',this.form.E);
              params.append('score',this.form.score);
                params.append('answer',this.form.answer);
                params.append('faq',this.form.faq);
                this.axios.post('http://localhost:8089/item/add',params)
                .then( res => {
                    loadingInstance.close();
                    this.$message.success('添加成功！');
                  bus.$emit("addClose",{type:this.type});
                })
                .catch( res => {
                    loadingInstance.close();
                    this.$message.error('添加失败！');
                })
                if (this.isLast) {
                    bus.$emit("isLast",true);
                }
                this.handleClose();
            },
            handleClose () {
                this.dialogFormAddSubjectVisible = false;
                  for (var index in this.form) {   //关闭弹框后把内容清空
                        if (index == 'questionImg' || index == 'answerImg') {
                            this.form[index] = {};
                        } else {
                            this.form[index] = '';
                        }
                    }
                    this.question_fileList = [];
                    this.answer_fileList = [];
                    this.questionImgCounter = 0;
                    this.answerImgCounter = 0;
                    this.questionImgNameAry = [];
                    this.answerImgNameAry = [];
            },
            handleQuestionChange (file,fileList) { //问题图片处理方法
                this.handleImgChange('questionImgCounter','questionImgNameAry','question_','questionImg',file);
            },
            handleAnswerChange (file,fileList) {//答案图片处理方法
                this.handleImgChange('answerImgCounter','answerImgNameAry','answer_','answerImg',file);
            },
            handleImgChange(ImgCounter,imgNameAry,type,imgType,file) { //图片处理公共方法
                this[ImgCounter] ++;
                let loadingInstance2;
                let temp = this.form;   //转换完成后，在事件中的this指不向vue实例，所以这里首先拿到form对象
                this[imgNameAry].push(type + new Date().getTime() + file.raw.name);
                let target = this;
                let reader = new FileReader();
                reader.onloadend = function () {  //读取完毕后赋值
                    let Img = target[ImgCounter];
                    temp[imgType][Img] = reader.result;
                    loadingInstance2.close();  //关闭加载条
                }
                if (file.raw) {
                    loadingInstance2 = Loading.service({   //读取文件时显示加载条
                        target:'.el-dialog',
                        text:'图片上传中...'
                    });
                    reader.readAsDataURL(file.raw);   //读取文件
                } else {
                    console.log('error');
                }
            },

            //重新生成图片库
            reProductImg (imgType,fileList) {
                for (let i = 0; i < fileList.length; i ++) {
                    let loadingInstance2;
                    let temp = this.form;   //转换完成后，在事件中的this指不向vue实例，所以这里首先拿到form对象
                    let target = this;
                    let reader = new FileReader();
                    reader.onloadend = function () {  //读取完毕后赋值
                        temp[imgType][i+1] = reader.result;
                        loadingInstance2.close();  //关闭加载条
                    }
                    if (fileList[i].raw) {
                        loadingInstance2 = Loading.service({   //读取文件时显示加载条
                            target:'.el-dialog',
                            text:'图片上传中...'
                        });
                        reader.readAsDataURL(fileList[i].raw);   //读取文件
                    } else {
                        console.log('error');
                    }
                }
            },

            //将图片名称移除
            removeImgName (imgName,imgNameArr) {
                for (let i = 0; i < this[imgNameArr].length; i ++) {
                    if (this[imgNameArr][i].indexOf(imgName) > -1) {
                        this[imgNameArr].splice(i,1);
                    }
                }
            },

            //问题图片移除触发事件
            handleQuestionImgRemove(file,fileList) {
                this.questionImgCounter --;
                this.form.questionImg = {};
                if (this.questionImgCounter >= 0) {
                    this.removeImgName(file.name,'questionImgNameAry');
                    //重新生成图片库
                    this.reProductImg('questionImg',fileList);
                }
            },
            //答案图片移除触发事件
            handleAnswerImgRemove(file,fileList) {
                this.answerImgCounter --;
                this.form.answerImg = {};
                if (this.answerImgCounter >= 0) {
                    this.removeImgName(file.name,'answerImgNameAry');
                    //重新生成图片库
                    this.reProductImg('answerImg',fileList);
                }
            }
        },
        mounted() {    //在组件的钩子函数中监听事件
            bus.$on('addSubject', data => {   //获取值
                this.dialogFormAddSubjectVisible = data.flag;
                this.type = data.type;
                this.isLast = data.isLast;
                this.showChoice = data.option;
                this.showMuilt = data.muilt;
            })
        }
    }
</script>

<style scoped>

</style>




