<template>
    <div>
        <h1>导出试卷模块<br><a>(只能导出审核通过的试卷)</a></h1>
      <div class="table">
         <el-table
            :data="tableData"
            border
            stripe
            style="width: 100%;margin-top:10px"
            max-height="449"
            v-loading="loading"
            highlight-current-row
            :cell-style="styleFunc">
            <el-table-column
            prop="id"
            label="ID"
            width="60"
            align="center"
            show-overflow-tooltip>
            </el-table-column>
           <el-table-column
             prop="name"
             label="试卷名称"
             width="300"
             align="center"
             show-overflow-tooltip>
           </el-table-column>
            <el-table-column
            prop="time"
            label="生成时间"
            width="300"
            align="center"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
            label="操作"
            width="280"
            align="center">
                <template slot-scope="scope">
                    <el-button  size="mini" type="primary" @click="paper(scope.row.id)"><i class="el-icon-download">下载试卷</i></el-button>
                    <el-button  size="mini" type="primary" @click="answer(scope.row.id)"><i class="el-icon-download">下载答案</i></el-button>
                </template>
            </el-table-column>
        </el-table>
      </div>
    </div>
</template>

<script>
    import bus from './BUS.vue'

    export default {
        filters:{
            myname: (data,id) => {
                return  "数据库原理试卷" + id;
            },
            myhreftest: (data,id) => {
                return 'http://localhost:8089/download/paper/'+id;
            },
            myhrefanswer(val) {
              console.log(val);
                /*return 'http://localhost:8089/download/answer/' + val;*/
            }
        },
        data(){
            return {
               vdownload:'',
               vhref:'',
               currentPage:1,
               count:0,
               loading:true,
               tableData:[
                 {
                   id:1
                 }
               ]
            }
        },
        methods: {
          answer(val) {
          window.location.href = 'http://localhost:8089/download/answer/' + val
          },
          paper(val){
            window.location.href = 'http://localhost:8089/download/paper/' + val
          }
        },

        mounted(){  //获取试题文件列表
            this.axios.get('http://localhost:8089/paper/getPapers',{
                params: {
                }
            })
            .then( res => {
                this.loading = false;
                this.tableData = res.data.data;
            })
            .catch( res => {
                this.loading = false;
                console.log('error');
            })
        }
    }
</script>

<style scoped>
  .table {
    margin-left: 30px;
    margin-right: 30px;
  }
    h1{
        text-align: center;
      font-size: 25px;
    }
   h1 a{
      font-size: 15px;
     color: red;
    }
    a {
        color: #179ed4;
        padding-left: 15px;
    }
</style>




