<template>
  <div>
    <div class="myaction">
      <div class="first">
        <div class="right">

        </div>
        <div class="clearfix"></div>
      </div>
      <div class="clearfix"></div>

      <div class="bottom">


        <div class="clearfix"></div>
      </div>
      <div class="clearfix"></div>
    </div>

    <div class="projectselect">
      <span>章节：</span>
      <el-select v-model="key1" placeholder="请选择章节..." @change="getPoint(key1)">
        <el-option
          v-for="item in chapterData"
          :key="item.id"
          :key1="item.id"
          :label="item.value"
          :value="item.id"
        ></el-option>
      </el-select>
    </div>
    <div class="projectselect">
      <span>题型：</span>
      <el-select v-model="key2" placeholder="请选择题型..">
        <el-option
          v-for="item in typeData"
          :key="item.id"
          :key2="item.id"
          :label="item.value"
          :value="item.id"
        ></el-option>
      </el-select>
    </div>
      <el-table
        :data="pointData"
        border
        stripe
        style="width: 100%"
        max-height="449"
        highlight-current-row
        :cell-style="styleFunc">
        <el-table-column
          prop="id"
          label="ID"
          width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="value"
          label="知识点"
          width="500"
          align="center"
          show-overflow-tooltip>
          <template scope="scope">
            <div style="color:deepskyblue;text-decoration:underline;cursor:pointer;" @click="getPointItem(scope.row.id)">{{ scope.row.value}}</div>
          </template>
        </el-table-column>
      </el-table>


    <div v-if="showItem">
      <h3>该知识点题目：</h3>
      <el-table
        :data="tableData"
        @selection-change="handleSelectionChange"
        border
        stripe
        style="width: 100%"
        max-height="449"
        highlight-current-row
        :cell-style="styleFunc">
        <el-table-column
          type="selection"
          width="55"
          align="center">
        </el-table-column>
        <el-table-column
          prop="id"
          label="ID"
          width="60"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="knowledge"
          label="知识点"
          width="120"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="courseId"
          label="所属章节"
          width="150"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="question"
          label="问题描述"
          width="200"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="answer"
          label="答案"
          width="180"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          v-if="showOption"
          prop="optioncontent"
          label="选项"
          width="350"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="faq"
          label="备注"
          width="180"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
     <!-- <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="count">
        </el-pagination>
      </div>-->
    </div>
    </div>
</template>

<script>
  import bus from './BUS.vue'

  export default {
    data () {
      return {
        pointData:[],
        showItem:false,
        key1:'',
        key2:'',
        typeData:[
          {
            "id":10001,
            "value":"单选题"
          },
          {
            "id":10002,
            "value":"多选题"
          },
          {
            "id":10003,
            "value":"填空题"
          },
          {
            "id":10004,
            "value":"判断题"
          },
          {
            "id":10005,
            "value":"问答题"
          }
        ],
        chapterData:[
          {
            "id":1,
            "value":"第一章"
          },
          {
            "id":2,
            "value":"第二章"
          },
          {
            "id":3,
            "value":"第三章"
          },
          {
            "id":4,
            "value":"第四章"
          },
          {
            "id":5,
            "value":"第五章"
          },
          {
            "id":6,
            "value":"第六章"
          },
          {
            "id":7,
            "value":"第七章"
          },
          {
            "id":8,
            "value":"第八章"
          },{
            "id":9,
            "value":"第九章"
          },
          {
            "id":10,
            "value":"第十章"
          }
        ],
        showOption:true,
        currentPage:1,
        pageSize:10,
        count:0,
        currentLimit:200,
        searchContent:"",
        multipleSelection: [],
        form:"",
        tableData:[],
        isSerach:false,  //是否为提交搜索
        type:'',
        isLast:false,
        loading:true,
        showMuilt:false
      }
    },
    methods:{
      getPointItem(val){
        if(this.key2 == ''){
          this.$message.error('选择题型后方可查看题目！')
        }
        else{
          this.axios.get('http://localhost:8089/chapter/getPointItems',{
            params:{
              type:this.key2,
              point:val
            }
          })
            .then(res =>{
              this.showItem = true;
              this.tableData = [];
              this.tableData = res.data.data;
              this.count = res.data.data.length;
            })
            .catch(res =>{
              console.log('error');
            })
        }
      },
      baiDU(){   //测试
        this.axios.get('http://localhost:8089/paperorganize/baidu')
          .then(function (response) {
            console.log(response);
          })
      },
      getPoint(val){
        this.axios.get('http://localhost:8089/point/getPoint',{
          params:{
            chapterId:val
          }
        })
          .then(res =>{
            this.pointData = [];
            this.pointData = res.data.data;
          })
      },
      onSubmit(){  //提交搜索内容
        if ( this.searchContent == "") {
          this.$message.error('搜索内容不能为空！');
        }else{
          this.loading = true;
          this.isSerach = true;
          this.axios.get('http://localhost:8089/searchQuestions',{
            params: {
              data:this.searchContent
            }
          })
            .then( res => {
              this.loading = false;
              this.tableData = [];
              this.tableData = res.data.data;
              this.count = res.data.data.length;
              this.currentPage = 1;
            })
            .catch( res => {
              this.loading = false;
              console.log("error");
            })
        }
      },
      handleLook(param){  //点击查看时向lookinfo发送消息
        bus.$emit("look", {flag:true,value:param,choice:this.showOption});
      },
      handleEdit(param){   //点击编辑时向editinfo发送消息
        bus.$emit("edit", {flag:true,value:param,type:this.type});
      },
      handleDel(id){   //删除一条记录
        this.$confirm('此操作将永久删除该题目, 是否继续?', '提示',{
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.loading = true;
          let params = new URLSearchParams();
          params.append("type",this.type);
          params.append("id",id);
          console.log(params);
          this.axios.post('http://localhost:8089/item/delete',params)
            .then( res => {
              this.$message.success('删除成功！');
              //删除成功后重新请求当前页
              this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
                params: {
                  type:this.type
                }
              })
                .then( res => {
                  this.loading = false;
                  this.tableData = [];
                  this.tableData = res.data.data;
                  this.count = res.data.count;
                })
                .catch( res => {
                  this.loading = false;
                  console.log("error");
                })
            })
            .catch( res => {
              this.loading = false;
              this.$message.error('删除失败！');
            })
        }).catch(() => {
        });
      },
      handerAddSubject() {  //添加一条题目
        if ( this.currentPage == Math.ceil( this.count / this.currentLimit) ) //判断是否是最后一页
          this.isLast = true;
        if (this.type ==10002){
          this.showMuilt = true;
        }
        bus.$emit("addSubject", {flag:true,type:this.type,isLast:this.isLast,option:this.showOption,muilt:this.showMuilt});
      },
      handleSizeChange(val) {  //更改每页的数量,重新发起请求
        this.loading = true;
        this.pageSize = val;
        if (this.isSerach) {    //如果是搜索页
          this.axios.get('http://172.20.10.14:8089/searchQuestions',{
            params: {
              data:this.searchContent
            }
          })
            .then( res => {
              this.loading = false;
              this.tableData = [];
              this.tableData = res.data.data;
              this.count = res.data.data.length;
              this.currentPage = 1;
            })
            .catch( res => {
              this.loading = false;
              console.log("error");
            })
        } else {   //平常页
          this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
            params: {
              type:this.type
            }
          })
            .then( res => {
              this.loading = false;
              this.tableData = res.data;
              this.count = res.data.data.length;
            })
            .catch( res => {
              this.loading = false;
              console.log("error");
            })
        }
      },
      handleCurrentChange(val) {  //更改当前页,重新发起请求
        this.loading = true;
        this.currentPage = val;
        if (this.isSerach) {   //如果是搜索内容页面切换
          this.axios.get('http://172.20.10.14:8089/searchQuestions',{
            params: {
              data:this.searchContent
            }
          })
            .then( res => {
              this.loading = false;
              this.tableData = [];
              this.tableData = res.data.data;
              this.count = res.data.data.length;
            })
            .catch( res => {
              this.loading = false;
              console.log("error");
            })
        } else {
          this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
            params: {
              type:this.type
            }
          })
            .then( res => {
              this.loading = false;
              this.tableData = res.data.data;
              this.count = res.data.data.length;
              this.currentPage = val;
            })
            .catch( res => {
              this.loading = false;
              console.log("error");
            })
        }
      },
      styleFunc(row,rowIndex){   //更改样式
        return {padding:'5px 0',hieght:'23px',lineHeight:'23px'}
      },
      handleSelectionChange(val) {   //处理多选框变化
        this.multipleSelection = val;
      },
      batchDel () {  //批量删除
        var delTarget = this.multipleSelection;
        var count = delTarget.length;
        var str = "";
        if (count == 0) {
          this.$message.error("请至少选择一项删除");
          return false;
        } else {
          for ( var i = 0 ; i < count; i ++) {//将需要删除的id凭借为字符串发往服务器
            str += delTarget[i].id + ",";
          }
          this.$confirm('此操作将永久删除这些题目, 是否继续?', '提示',{
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.loading = true;
            var params = new URLSearchParams();
            params.append("type",this.type);
            params.append("data",str);
            console.log(params);
            this.loading = true;
            this.axios.post('http://localhost:8089/item/bachDelete',params)
              .then( res => {
                this.$message.success('删除成功！');
                //删除成功后其重新请求当前页
                this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
                  params: {

                    type:this.type
                  }
                })
                  .then( res => {
                    this.loading = false;
                    this.tableData = [];
                    this.tableData = res.data.data;
                    this.count = res.data.data.length;
                  })
                  .catch( res => {
                    this.loading = false;
                    console.log("error");
                  })
              })
              .catch( res => {
                this.loading = false;
                this.$message.error('删除失败！');
              })
          }).catch(() => {
          })
        }
      },
      getItems(){
        if(this.key1 == ''|| this.key2 == ''){
          this.$message.error('请选择章节和题型！');
        }
        else{
          this.axios.get('http://localhost:8089/chapter/getChapterItems',{
            params:{
              type:this.key2,
              chapter:this.key1
            }
          })
            .then(res =>{
              this.showItem = true;
              this.tableData = [];
              this.tableData = res.data.data;
              this.count = res.data.data.length;
            })
            .catch(res =>{
              console.log('error');
            })
        }
      }
    }
  }
</script>

<style scoped>

  .myaction {
    margin-top: 15px;
  }

  .first {
    vertical-align: top;
  }

  .left {
    margin-left: 10px;
  }

  .left div {
    display: inline-block;
  }

  .left span,
  .left div,
  .left button {
    float: left;
    margin-left: 20px;
  }

  .left span {
    display: inline-block;
    height: 38px;
    line-height: 38px;
  }

  .right {
    float: right;
    margin-right: 25px;
  }

  .bottom {
    margin: 10px 20px;
  }

  .bottom button {
    margin-right: 10px;
  }
  .clearfix{
    clear:both;
  }

  .myelform{
    margin-bottom: 5px;
  }

  .block{
    margin:10px;
  }

  .projectselect {
    margin-top: 20px;
    margin-bottom: 10px;
  }

  .projectselect span {
    padding: 0 15px;
  }
</style>




