<template>
    <div>
        <div class="myaction">
            <div class="first">
                <div class="left">
                <!--<el-form :inline="true" class="demo-form-inline">
                        <el-form-item label="搜索内容" class="myelform">
                            <el-input v-model="searchContent" placeholder="请输入题目名称"></el-input>
                        </el-form-item>
                        <el-form-item  class="myelform">
                            <el-button type="primary" @click="onSubmit" icon="el-icon-search">查询</el-button>
                        </el-form-item>
                    </el-form>-->
                </div>
                <div class="right">

                </div>
                <div class="clearfix"></div>
            </div>
            <div class="clearfix"></div>

            <div class="bottom">
                <div class="left">

                    <el-button icon="el-icon-plus" type="primary" @click="handerAddSubject">添加题目</el-button>
                </div>

                <div class="clearfix"></div>
            </div>
            <div class="clearfix"></div>
        </div>

        <div class="projectselect">
            <span>题型：</span>
            <el-select v-model="type" placeholder="请选择题型..">
                <el-option label="单选题" value="10001" selected></el-option>
                <el-option label="多选题" value="10002"></el-option>
                <el-option label="填空题" value="10003"></el-option>
                <el-option label="判断题" value="10004"></el-option>
                <el-option label="问答题" value="10005"></el-option>
            </el-select>
        </div>

        <el-table
        :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)"
        @selection-change="handleSelectionChange"
        border
        stripe
        style="width: 100%"
        max-height="449"
        highlight-current-row
        :cell-style="styleFunc">
            <el-table-column
            type="selection"
            width="55"
            align="center">
            </el-table-column>
            <el-table-column
            prop="id"
            label="ID"
            width="60"
            align="center"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
            prop="knowledge"
            label="知识点"
            width="150"
            align="center"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
            prop="courseId"
            label="所属章节"
            width="120"
            align="center"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
            prop="question"
            label="问题描述"
            width="250"
            align="center"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
            prop="answer"
            label="答案"
            width="120"
            align="center"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              v-if="showOption"
            prop="optioncontent"
            label="选项"
            width="350"
            align="center"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
            prop="faq"
            label="备注"
            width="120"
            align="center"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
            label="操作"
            width="182"
            align="center">
                <template slot-scope="scope">
                    <el-tooltip class="item" effect="light" content="查看" placement="bottom-end">
                        <el-button @click="handleLook(scope.row)" type="info" size="mini"><i class="el-icon-view"></i></el-button>
                    </el-tooltip>
                    <el-tooltip class="item" effect="light" content="编辑" placement="bottom-end">
                        <el-button  size="mini" type="primary" @click="handleEdit(scope.row)"><i class="el-icon-edit"></i></el-button>
                    </el-tooltip>
                    <el-tooltip class="item" effect="light" content="删除" placement="bottom-end">
                        <el-button  size="mini" type="danger" @click="handleDel(scope.row.id)"><i class="el-icon-delete"></i></el-button>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>

        <!-- 分页 -->
        <div class="block">
            <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 10, 20, 30]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="count">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import bus from './BUS.vue'

    export default {
        data () {
            return {
              showOption:true,
                currentPage:1,
                pageSize:5,
                count:0,
                currentLimit:200,
                searchContent:"",
                multipleSelection: [],
                form:"",
                tableData:[],
                isSerach:false,  //是否为提交搜索
                type:'',
                isLast:false,
                loading:true,
              showMuilt:false
            }
        },
        methods:{
            baiDU(){   //测试
                this.axios.get('http://localhost:8089/paperorganize/baidu')
                .then(function (response) {
                console.log(response);
                })
                    },

            onSubmit(){  //提交搜索内容
                if ( this.searchContent == "") {
                    this.$message.error('搜索内容不能为空！');
                }else{
                    this.loading = true;
                    this.isSerach = true;
                    this.axios.get('http://localhost:8089/searchQuestions',{
                        params: {
                            data:this.searchContent
                        }
                    })
                    .then( res => {
                        this.loading = false;
                        this.tableData = [];
                        this.tableData = res.data.data;
                      this.count = res.data.data.length;
                        this.currentPage = 1;
                    })
                    .catch( res => {
                        this.loading = false;
                        console.log("error");
                    })
                }
            },
            handleLook(param){  //点击查看时向lookinfo发送消息
                bus.$emit("look", {flag:true,value:param,choice:this.showOption});
            },
            handleEdit(param){   //点击编辑时向editinfo发送消息
              if (this.type ==10002){
                this.showMuilt = true;
              }
                bus.$emit("edit", {flag:true,value:param,type:this.type,muilt: this.showMuilt});
            },
            handleDel(id){   //删除一条记录
                this.$confirm('此操作将永久删除该题目, 是否继续?', '提示',{
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.loading = true;
                    let params = new URLSearchParams();
                        params.append("type",this.type);
                        params.append("id",id);
                        console.log(params);
                        this.axios.post('http://localhost:8089/item/delete',params)
                        .then( res => {
                            this.$message.success('删除成功！');
                            //删除成功后重新请求当前页
                            this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
                                params: {
                                    type:this.type
                                }
                            })
                            .then( res => {
                                this.loading = false;
                                this.tableData = [];
                                this.tableData = res.data.data;
                                this.count = res.data.count;
                            })
                            .catch( res => {
                                this.loading = false;
                                console.log("error");
                            })
                        })
                        .catch( res => {
                            this.loading = false;
                            this.$message.error('删除失败！');
                        })
                }).catch(() => {
                });
            },
            handerAddSubject() {  //添加一条题目
                if ( this.currentPage == Math.ceil( this.count / this.currentLimit) ) //判断是否是最后一页
                    this.isLast = true;
                if (this.type ==10002){
                  this.showMuilt = true;
                }
                if(this.type == '')
                {
                  this.$message.error('请选择题型后再点添加！')
                }
                else
                  bus.$emit("addSubject", {flag:true,type:this.type,isLast:this.isLast,option:this.showOption,muilt:this.showMuilt});
            },
            handleSizeChange(val) {  //更改每页的数量,重新发起请求
                this.loading = true;
                this.pageSize = val;
                if (this.isSerach) {    //如果是搜索页
                    this.axios.get('http://172.20.10.14:8089/searchQuestions',{
                        params: {
                            data:this.searchContent
                        }
                    })
                    .then( res => {
                        this.loading = false;
                        this.tableData = [];
                        this.tableData = res.data.data;
                      this.count = res.data.data.length;
                        this.currentPage = 1;
                    })
                    .catch( res => {
                        this.loading = false;
                        console.log("error");
                    })
                } else {   //平常页
                    this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
                    params: {
                        type:this.type
                    }
                })
                .then( res => {
                    this.loading = false;
                    this.tableData = res.data;
                  this.count = res.data.data.length;
                })
                .catch( res => {
                    this.loading = false;
                    console.log("error");
                })
                }
            },
            handleCurrentChange(val) {  //更改当前页,重新发起请求
                this.loading = true;
                this.currentPage = val;
                if (this.isSerach) {   //如果是搜索内容页面切换
                    this.axios.get('http://172.20.10.14:8089/searchQuestions',{
                        params: {
                            data:this.searchContent
                        }
                    })
                    .then( res => {
                        this.loading = false;
                        this.tableData = [];
                        this.tableData = res.data.data;
                      this.count = res.data.data.length;
                    })
                    .catch( res => {
                        this.loading = false;
                        console.log("error");
                    })
                } else {
                    this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
                        params: {
                            type:this.type
                        }
                    })
                    .then( res => {
                        this.loading = false;
                        this.tableData = res.data.data;
                      this.count = res.data.data.length;
                      this.currentPage = val;
                    })
                    .catch( res => {
                        this.loading = false;
                        console.log("error");
                    })
                }
            },
            styleFunc(row,rowIndex){   //更改样式
                return {padding:'5px 0',hieght:'23px',lineHeight:'23px'}
            },
            handleSelectionChange(val) {   //处理多选框变化
                this.multipleSelection = val;
            },
            batchDel () {  //批量删除
                var delTarget = this.multipleSelection;
                var count = delTarget.length;
                var str = "";
                if (count == 0) {
                    this.$message.error("请至少选择一项删除");
                    return false;
                } else {
                    for ( var i = 0 ; i < count; i ++) {//将需要删除的id凭借为字符串发往服务器
                        str += delTarget[i].id + ",";
                    }
                    this.$confirm('此操作将永久删除这些题目, 是否继续?', '提示',{
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.loading = true;
                        var params = new URLSearchParams();
                        params.append("type",this.type);
                        params.append("data",str);
                        console.log(params);
                        this.loading = true;
                        this.axios.post('http://localhost:8089/item/bachDelete',params)
                        .then( res => {
                                this.$message.success('删除成功！');
                                //删除成功后其重新请求当前页
                                this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
                                    params: {

                                        type:this.type
                                    }
                                })
                                .then( res => {
                                    this.loading = false;
                                    this.tableData = [];
                                    this.tableData = res.data.data;
                                  this.count = res.data.data.length;
                                })
                                .catch( res => {
                                    this.loading = false;
                                    console.log("error");
                                })
                        })
                        .catch( res => {
                            this.loading = false;
                            this.$message.error('删除失败！');
                        })
                    }).catch(() => {
                    })
                }
            }
        },
        mounted(){ //加载页面时默认请求第一类型数据
          bus.$on('addClose',function (data) {
            this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
              params: {

                type:data.type
              }
            })
              .then( res => {
                this.tableData = res.data.data;
                this.count = res.data.data.length;
                this.currentPage = Math.ceil(this.count/this.pageSize)
              })
              .catch( res => {
                console.log("error");
              })
          })
            var target = this;
            //监听增加题目发来的信息
            bus.$on('isLast', function (data) {
                this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
                    params: {

                        type:target.type
                    }
                })
                .then( res => {
                    target.tableData = res.data.data;
                  target.count = res.data.data.length;
                })
                .catch( res => {
                    console.log("error");
                })
            });
        },
        watch:{    //更换题型重新发起请求
            type: function (newValue) {
              if (newValue != 10001 && newValue != 10002){
                console.log(newValue);
                this.showOption = false;
              }
                this.loading = true;
                this.currentPage = 1;
                this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
                    params: {
                        type:newValue
                    }
                })
                .then( res => {
                  if(res.data.data.length != 0) {
                    this.loading = false;
                    this.tableData = res.data.data;
                    this.count = res.data.data.length;
                  }
                  else{
                    this.tableData = [];
                    this.$message.error("数据为空");
                  }

                })
                .catch( res => {
                this.loading = false;
                    console.log("error");
                })
            }
        }
    }
</script>

<style scoped>

    .myaction {
        margin-top: 15px;
    }

    .first {
        vertical-align: top;
    }

    .left {
        margin-left: 10px;
    }

    .left div {
        display: inline-block;
    }

    .left span,
    .left div,
    .left button {
        float: left;
        margin-left: 20px;
    }

    .left span {
        display: inline-block;
        height: 38px;
        line-height: 38px;
    }

    .right {
        float: right;
        margin-right: 25px;
    }

    .bottom {
        margin: 10px 20px;
    }

    .bottom button {
        margin-right: 10px;
    }
    .clearfix{
        clear:both;
    }

    .myelform{
        margin-bottom: 5px;
    }

    .block{
        margin:10px;
    }

    .projectselect {
        margin-top: 20px;
        margin-bottom: 10px;
    }

    .projectselect span {
        padding: 0 15px;
    }
</style>




