<template>
    <div id="upload">
        <div>
            <el-select v-model="region" placeholder="请选择题型">
                <el-option label="单选题" value="0"></el-option>
                <el-option label="多选题" value="1"></el-option>
                <el-option label="填空题" value="2"></el-option>
                <el-option label="判断题" value="3"></el-option>
                <el-option label="问答题" value="4"></el-option>
            </el-select>
        </div>
      <el-upload
        :show-file-list="true"
        accept="application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
        action="/employee/basic/importEmp"
        :data="{id:region}"
        :on-success="fileUploadSuccess"
        :on-error="fileUploadError" :disabled="fileUploadBtnText=='正在导入'"
        :before-upload="beforeFileUpload" style="display: inline">
        <el-button size="mini" type="success" :loading="fileUploadBtnText=='正在导入'"><i class="fa fa-lg fa-level-up"
                                                                                      style="margin-right: 5px"></i>{{fileUploadBtnText}}
        </el-button>
        <div slot="tip" class="el-upload__tip">只能上传.xls/.xlsx文件，且不超过500kb</div>
      </el-upload>
        <el-upload
        class="upload-demo"
        drag
        accept="application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
        :limit="number"
        auto-upload
        action="http://localhost:8089/upload"
        :on-success="handleSuccess"
        :on-error="handleError"
        multiple
        :data="{id:region}">
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        <div class="el-upload__tip" slot="tip">只能上传doc/doxc文件</div>
        </el-upload>
    </div>
 <!-- <el-button @click="getPaper">下载</el-button>-->
</template>

<script>
    export default {
        data(){
            return {
                region:'',
                fileUploadBtnText:'导入数据',
                dataObj:{
                    id:this.region
                }
            }
        },
        methods:{
          fileUploadSuccess(response, file, fileList){
            if (response) {
              this.$message({type: response.status, message: response.msg});
            }
            this.loadEmps();
            this.fileUploadBtnText = '导入数据';
          },
          fileUploadError(err, file, fileList){
            this.$message({type: 'error', message: "导入失败!"});
            this.fileUploadBtnText = '导入数据';
          },
          beforeFileUpload(file){
            this.fileUploadBtnText = '正在导入';
          },
           handleSuccess(){
               this.$message.success('上传成功！');
           },
           handleError(){
               this.$message.error('上传失败！');
           },
         /* getPaper(){
            this.axios.get('http://localhost:8089/download/download',{
              params:{
              id:1001
              }
            })
              .then
          }*/
        }
    }
</script>

<style scoped>
    #upload{
        margin-top: 150px;
        margin-left: 350px;
    }
    .upload-demo{
        margin-top:15px;
    }
</style>




